import React from "react";
import Context from "../../Context";
import {injectIntl} from "react-intl";
import key from "../../util/key";
import {PlusOutlined, SearchOutlined} from "@ant-design/icons";
import DataTable from "../../components/Datatable/DataTable";
import DataTableSearch from "../../components/Datatable/DataTableSearch";
import Statelabel from "../../util/Statelabel";
import {Button} from "antd";
import EditModal from "../../util/EditModal";
import tariffEditForm from "./tariffEditForm";
import DataTablePeriod from "../../components/Datatable/DataTablePeriod";
import {Helmet} from "react-helmet";

class Tarrifs extends React.Component {
  static contextType = Context;

  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],

      editModalVisible: false,
      editModalId: null,
    };
  }

  m = (key, prefix = "tariff.") => {
    const {intl} = this.props;
    return intl.formatMessage({id: prefix + key});
  };

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  onMethodFinish = () => {
    this.setState({
      tableUuid: key(),
    });
    if (this.props.onMethodFinish) {
      this.props.onMethodFinish();
    }
  };

  editModalToggle = (editModalId = null) => {
    this.setState((state) => {
      return {editModalVisible: !state.editModalVisible, editModalId};
    });
  };

  editModalFinish = () => {
    this.editModalToggle();
    this.onMethodFinish();
  };

  setColumns = () => {
    this.setState({
      columns: [
        {
          text: true,
          title: (
            <>
              <Button
                className="gx-mr-4"
                onClick={(e) => {
                  e.stopPropagation();
                  this.editModalToggle();
                }}
                icon={<PlusOutlined/>}
              />
              {this.m("identity")}
            </>
          ),
          dataIndex: "label",
          sorter: true,
          width: "15%",
          render: (value, item) => (
            <>
              <div
                className="gx-pointer gx-text-primary"
                onClick={() => this.editModalToggle(item.id)}
              >
                <div>{value}</div>
                <div>{item.code}</div>

                {item.description && (
                  <div className="gx-font-weight-light gx-text-small gx-text-muted">
                    {item.description}
                  </div>
                )}
              </div>
            </>
          ),

          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },

        {
          text: true,
          title: this.m("networkname"),
          dataIndex: "networkname",
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            <div>{item.networkname}</div>

            {item.networkdescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.networkdescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("servicename"),
          dataIndex: "servicecode",
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            <div>{item.servicename}</div>

            {item.servicedescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.servicedescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },
        {
          text: true,
          title: this.m("modename"),
          dataIndex: "modecode",
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            <div>{item.modename}</div>

            {item.modedescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.modedescription}
              </div>
            )} </>,
          filters: [
            {
              text: this.m("mode.slow"),
              value: "slow.mode",
            },
            {
              text: this.m("mode.standard"),
              value: "standard.mode",
            },
            {
              text: this.m("mode.fast"),
              value: "fast.mode",
            },
            {
              text: this.m("mode.ultra"),
              value: "ultra.mode",
            },
          ],
        },

        {
          text: true,
          title: this.m("currency"),
          dataIndex: "currencycode",
          sorter: true,
          width: "10%",
          render: (value, item) => <>
            <div>{item.currencycode} ({item.currencyname})</div>

            {item.currencydescription && (
              <div className="gx-font-weight-light gx-text-small gx-text-muted">
                {item.currencydescription}
              </div>
            )} </>,
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{color: filtered ? "#1890ff" : undefined}}
            />
          ),
        },

        {
          text: true,
          width: "5%",
          title: this.m("price"),
          dataIndex: "price",
          render: (value, item) => <>{value}</>,
        },
        {
          text: true,
          width: "5%",
          title: this.m("commission"),
          dataIndex: "commission",
          render: (value, item) => <>{value ? `${value}` : "0"}</>,
        },
        {
          text: true,
          width: "5%",
          title: this.m("cost"),
          dataIndex: "cost",
          render: (value, item) => <>{value}</>,
        },
        {
          text: true,
          width: "5%",
          title: this.m("linschedule"),
          dataIndex: "linschedule",
          render: (value, item) => <>{value ? `${value}` : "No"}</>,
        },
        {
          width: "5%",
          title: this.m("lastupdate"),
          dataIndex: "lastupdate",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br/>
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },

          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          width: "5%",
          title: this.m("statename"),
          dataIndex: "state",
          sorter: true,
          render: (value, item) => (
            <div className="gx-text-right">
              <Statelabel onFinish={this.onMethodFinish} {...item} />
            </div>
          ),
        },
      ],
    });
  };

  render() {
    return (
      <div>
        <Helmet>
          <title>{this.m("tariffs")}</title>
        </Helmet>
        <h2 className="title gx-mb-4">{this.m("tariffs")}</h2>
        <EditModal
          essence="tariff"
          onClose={this.editModalToggle}
          form={tariffEditForm.call(this)}
          onFinish={this.editModalFinish}
          visible={this.state.editModalVisible}
          id={this.state.editModalId}
        />
        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/tariff/list"}
        />
      </div>
    );
  }
}

export default injectIntl(Tarrifs);
