import React from "react";
import { Form, Input } from "antd";
import NumericInput from "../../util/NumericInput";
import PlainSelect from "../../util/PlainSelect";
const { TextArea } = Input;

export default function tariffEditForm() {
  let formItems = {
    label: {},
    type: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("type"),
      component: <PlainSelect labelKey="name" cache query="/tariff/type" />,
    },
    network: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("network"),
      component: <PlainSelect labelKey="label" cache query="/network/list" />,
    },
    service: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("service"),
      component: <PlainSelect labelKey="name" cache query="/service/list" />,
    },
    mode: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("mode"),
      component: <PlainSelect labelKey="name" cache query="/mode/list" />,
    },

    currency: {
      rules: [
        {
          required: true,
        },
      ],
      label: this.m("currency"),
      component: <PlainSelect labelKey="name" cache query="/currency/list" />,
    },

    price: {
      component: <NumericInput />,
    },
    commission: {
      component: <NumericInput />,
    },
    cost: {
      component: <NumericInput />,
    },
    description: {
      component: <TextArea />,
    },
  };
  return (
    <>
      {Object.entries(formItems).map(([key, item]) => {
        let label = item.label || this.m(key);
        let component = item.component || <Input />;

        return (
          <Form.Item key={key} rules={item.rules} label={label} name={key}>
            {component}
          </Form.Item>
        );
      })}
    </>
  );
}
